// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as Private from '../../src/hd/private.js'
import * as Public from '../../src/hd/public.js'
import fixtures from './fixtures/private.json'

const { network } = fixtures

describe('Testing HD Private Key', function () {
  fixtures.string.forEach(test => {
    const base58Key = test[0]
    const privateKey = {
      privateKey: test[1],
      version: test[2],
      depth: test[3],
      parentFingerPrint: test[4],
      childNumber: test[5],
      chainCode: test[6]
    }

    it(`Extended key from string: ${test[0]}`, function () {
      const resultedXkey = Private.fromString(base58Key, network)
      assert.deepEqual(resultedXkey, privateKey)
    })

    it(`Extended key to string: ${test[1]}`, function () {
      const resultedBase58Key = Private.toString(privateKey, network)
      assert.equal(resultedBase58Key, base58Key)
    })
  })

  fixtures.fromIndex.forEach(test => {
    it(`Deriving key ${test[0]} for index ${test[2]}`, async function () {
      const privateKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const publicKey = test[6]
      const expectedChildKey = {
        privateKey: test[3],
        chainCode: test[4],
        childNumber: test[5]
      }
      const xKey = Private.fromExtendedKey({ privateKey, chainCode })
      const childKey = await Private.fromIndex(xKey, index, publicKey)
      assert.deepEqual({
        privateKey: childKey.privateKey,
        chainCode: childKey.chainCode,
        childNumber: childKey.childNumber
      }, expectedChildKey)
    })
  })

  fixtures.fromPath.forEach(test => {
    it(`Deriving HD key ${test[0]} from HD Settings ${
      test[3]
    }`, async function () {
      const xkey = test[0]
      const parentKey = Private.fromString(xkey, network)

      let account = 0
      let coinType = 0
      let bips = test[2]
      if (!bips) bips = [84]
      if (test[1]) {
        account = test[1][0]
        coinType = test[1][1]
      }
      let testIndex = 3

      const testPath = async (path) => {
        const hdKey = await Private.fromPath(parentKey, path)
        const xprivkey = Private.toString(hdKey, network)
        const pubKey = await Private.toPublic(hdKey)
        const xpubkey = Public.toString(pubKey, network)
        assert.equal(xprivkey, test[testIndex++], 'xpriv')
        assert.equal(xpubkey, test[testIndex++], 'xpub')
      }

      for (const purpose of bips) {
        if (purpose === 32) {
          await testPath(['m', `${account}`, 0].join('/'))
          await testPath([ account, 0 ])
        } else {
          await testPath(['m', `${purpose}'`, `${coinType}'`, `${account}'`, 0].join('/'))
          await testPath(['m', `${purpose}'`, `${coinType}'`, `${account}'`, 1].join('/'))
        }
      }
    })
  })
})
